.container {
  max-width: 100vw;
  display: grid;
  grid-template-columns: 60px 1fr 1fr;
  justify-content: center;
}

@media (max-width: 1024px) {
  .container {
    max-width: 100vw;
    grid-template-columns: 0px 1fr 1fr;
  }
}


@media screen and (max-width: 500px) {
  .container {
    height: 100%;
    width: 100%;
    grid-template-rows: auto;
    position: fixed;
    overflow: hidden;
  }
}

@media screen and (max-width: 360px) {
  .container {
    height: 100%;
    width: 100%;
    grid-template-rows: auto;
    position: fixed;
    overflow: hidden;
  }
}